.toDoList{
    list-style-type: none;
    font: 18pt Arial;
    color: #222;
}

.toDoList .done {
    text-decoration: line-through;
    background-color: rgb(235, 233, 233);
}
.toDoList .important .fa-star{
    color: rgb(4, 130, 248);
}
.toDoList .done .fa-star{
    color: rgb(88, 88, 88);
}

.toDoList .important {
    font-weight: 700;
    font-style: italic;
    color:rgb(4, 130, 248);
}
li, .badge{
    cursor: pointer;
}

